


import React from 'react';
import { withRouter } from 'react-router-dom'

import { Layout } from 'element-react';
// import { Button } from 'element-react';  // 引入element-react

import '@/styles/header.css';


class header extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            hamburgerCount: 0,
            ifHamburgerShow: false,
        };
        this.goUrl = this.goUrl.bind(this);
        this.hamburger = this.hamburger.bind(this);

    }

    hamburger() {
        console.log(this.state.hamburgerCount)
        if (this.state.hamburgerCount % 2 === 0) {
            this.setState({ ifHamburgerShow: true })
            this.props.onShow(false);
        } else {
            this.setState({ ifHamburgerShow: false })
            this.props.onShow(true);
        }
        let count = this.state.hamburgerCount;
        count += 1;
        this.setState({ hamburgerCount: count })

    }
    goUrl(num) {
        let url;
        if (num === 0){
            url = "https://www.veritas.com/zh/cn"
        }
        else if (num === 1) {
            url = "https://icampaign.com.cn/Veritas/index.php?m=VeritasWebinar&channel=DB"
        } else if (num === 2) {
            url = "https://icampaign.com.cn/Veritas/index.php?m=VeritasWebinar&a=pdf&channel=DB"
        } else if (num === 3) {
            url = ""
        } else if (num === 4) {
            url = "https://www.veritas.com/zh/cn"
        } else if (num === 5) {
            url = ""
        } else if (num === 6) {
            url = "https://icampaign.com.cn/Veritas/index.php?m=VeritasWebinar&a=reg&channel=DB"
        }

        window.location.href = url
    }
    render() {

        let logo_icon = require("@/assets/veritas/logo.png")
        let home_icon = require("@/assets/veritas/home.png")
        let weixin_icon = require("@/assets/veritas/weixin.png")
        let hamburger_icon = require("@/assets/veritas/hamburger.png")


        return (
            <div className="header">
                {/*  */}
                <Layout.Row className="sreenPC" gutter="0">
                    <Layout.Col span="2">&nbsp;</Layout.Col>
                    <Layout.Col span="4" className="left_logo"><img onClick={() => this.goUrl(0)} className="logo_icon" src={logo_icon} alt="" /></Layout.Col>
                    <Layout.Col span="2"><span onClick={() => this.goUrl(1)} className="header_title">&nbsp;&nbsp;&nbsp;首页大厅</span></Layout.Col>
                    <Layout.Col span="2"><span onClick={() => this.goUrl(2)} className="header_title">&nbsp;&nbsp;&nbsp;&nbsp;资料库</span></Layout.Col>
                    <Layout.Col span="2"><span onClick={() => this.goUrl(3)} className="header_title">&nbsp;在线提问</span></Layout.Col>
                    <Layout.Col span="2"><span onClick={() => this.goUrl(4)} className="header_title"><span className="mypic"><img className="home_icon" src={home_icon} alt="" /></span><span className="title_details">进入官网</span></span></Layout.Col>
                    <Layout.Col span="2"><span onClick={() => this.goUrl(5)} className="header_title"><span className="mypic"><img className="weixin_icon" src={weixin_icon} alt="" /></span><span className="title_details">官方微信</span></span></Layout.Col>
                    <Layout.Col span="2">&nbsp;</Layout.Col>
                    <Layout.Col span="4"><span onClick={() => this.goUrl(6)} className="header_title header_title2">登录&nbsp;丨&nbsp;注册</span></Layout.Col>
                    <Layout.Col span="2">&nbsp;</Layout.Col>
                </Layout.Row>

                <Layout.Row className="sreenPhone" gutter="0">
                    <Layout.Col span="2">&nbsp;</Layout.Col>
                    <Layout.Col span="3" className="left_logo"><img onClick={() => this.goUrl(0)} className="logo_icon" src={logo_icon} alt="" /></Layout.Col>
                    <Layout.Col span="14">&nbsp;</Layout.Col>
                    <Layout.Col span="3" className="hamburger"><img onClick={() => this.hamburger()} className="hamburger_icon" src={hamburger_icon} alt="" /></Layout.Col>
                    <Layout.Col span="2">&nbsp;</Layout.Col>
                </Layout.Row>

                {this.state.ifHamburgerShow &&
                    <div className="menu">
                        <p onClick={() => this.goUrl(1)} className="header_title">首页大厅</p>
                        <p onClick={() => this.goUrl(2)} className="header_title">资料库</p>
                        <p onClick={() => this.goUrl(3)} className="header_title">在线提问</p>
                        <p onClick={() => this.goUrl(4)} className="header_title"><span className="mypic"><img className="home_icon" src={home_icon} alt="" /></span><span className="title_details">进入官网</span></p>
                        <p onClick={() => this.goUrl(5)} className="header_title"><span className="mypic"><img className="weixin_icon" src={weixin_icon} alt="" /></span><span className="title_details">官方微信</span></p>
                    </div>
                }

                {/*  */}
            </div>
        )
    }
}

export default withRouter(header)
